export default function reducer(state = {
    userDetail: {},
    fetching: false,
    fetched: false,
    error: null,
    isLogin: false,
}, action) {

    switch (action.type) {
        case "USER_DETAIL": {
            return { ...state, fetching: true }
        }
        case "USER_DETAIL_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "USER_DETAIL_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                userDetail: action.payload,
                isLogin: true
            }
        }
    }

    return state
}
