import React, { Component } from 'react';
import { connect } from "react-redux"
import { Link } from "react-router-dom";
import './resultDetail.scss';
import M1 from '../../assets/images/m1.png';

const apiUrl = process.env.REACT_APP_API_URL;
const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;


class ResultDetail extends Component {
    constructor() {
        super();
        this.state = {
            imageUrl: null
        };
    }

    componentWillMount() {
        var token = localStorage.getItem('token');
        fetch(apiUrl + this.props.filmData.poster_url, {
            headers: new Headers({
                'Authorization': `Token ` + token,
                'Content-Type': 'application/json'
            })
        })
            .then(response => {
                if (response.statusText === 'OK') {
                    this.setState({ imageUrl: response.url })
                }
            })
    }


    render() {
        const { filmData } = this.props;
        var total = this.props.filmData.dbo + this.props.filmData.ibo;

        return (
            <div className="result-details active">
                <div className="left">
                    <div className="top">
                        <div className="image">
                            <img src={this.state.imageUrl ? this.state.imageUrl : M1} />
                        </div>
                    </div>
                    {/* <div className="bottom">
                        <div className="image-box">
                            <span className="title">Main Lead</span>
                            <div className="image">
                                <img src="images/grd.png" alt="image" />
                            </div>
                            <span className="name">Gerard Butler</span>
                        </div>
                        <div className="image-box">
                            <span className="title">Producer</span>
                            <div className="image">
                                <img src="images/grd.png" alt="image" />
                            </div>
                            <span className="name">Gerard Butler</span>
                        </div>
                    </div> */}
                </div>
                <div className="right">
                    <h3>{filmData.title}</h3>
                    <h4>STX Entertainment</h4>
                    <div className="m-detail responsive">
                        <span className="title">Main lead</span>
                        <span className="value">Gerard Butler</span>
                    </div>
                    <div className="m-detail responsive">
                        <span className="title">Producer</span>
                        <span className="value">Gerard Butler</span>
                    </div>
                    <div className="m-detail">
                        <span className="title">Release Date</span>
                        <span className="value">{filmData.domestic_release_date}</span>
                    </div>

                    <div className="m-detail">
                        <span className="title">Theaters</span>
                        <span className="value">??</span>
                    </div>
                    <div className="m-detail">
                        <span className="title">Close Date</span>
                        <span className="value">{filmData.domestic_release_date}</span>
                    </div>

                    <div className="m-detail">
                        <span className="title">Opening</span>
                        <span className="value price">?</span>
                    </div>
                    <div className="m-detail">
                        <span className="title">DOB</span>
                        <span className="value price">{filmData.dbo}</span>
                    </div>

                    <div className="m-detail">
                        <span className="title">Foreign</span>
                        <span className="value price">{filmData.ibo}</span>
                    </div>
                    <div className="m-detail">
                        <span className="title">Total</span>
                        <span className="value price total">{total}</span>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect((store) => {
    return {
        filmData: store.filmData.filmData,
    };
})(ResultDetail)

