export default function reducer(state = {
    key: {},
    fetching: false,
    fetched: false,
    error: null,
}, action) {

    switch (action.type) {
        case "VERIFY_EMAIL": {
            return { ...state, fetching: true }
        }
        case "VERIFY_EMAIL_REJECTED": {
            return { ...state, fetched: false, fetching: false, error: action.payload }
        }
        case "VERIFY_EMAIL_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                key: action.payload,
            }
        }
    }

    return state
}
