import React, { Component } from 'react';
import './compBuilder.scss'
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import MenuList from '../../components/menuList/menuList'
import SearchBox from '../../components/searchBox/searchBox'
import ResultDetail from '../../components/resultDetail/resultDetail'
import TableContainer from '../../components/tableContainer/tableContainer'

class CompBuilder extends Component {
    constructor() {
        super()
        this.state = {
            searchType: '',
        }
    }


    render() {
        const { searchType, addFilmBtn } = this.state;
        const { filmData, filmDataOpen } = this.props;

        return (
            <div className="hollywood-dashboard comp-builder-page">
                <MenuList />
                <div className="search-content">
                    <SearchBox searchType="film" searchType="film" />
                    {filmDataOpen ? <ResultDetail /> : ''}
                    <div className="back-layer"></div>
                    <div className="mylist">
                        <div className="title">
                            <span>My list</span>
                        </div>
                        <TableContainer />
                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(connect((store) => {
    return {
        filmData: store.filmData.filmData,
        filmDataOpen: store.filmData.filmDataOpen,
    };
})(CompBuilder))


