import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function passwordVerify(data, ownProps) {
    return function (dispatch) {
        dispatch({ type: "PASSWORD_VERIFY", ownProps });

        axios.post(apiUrl + '/apiv1/auth/password/reset/confirm/', data)
            .then((response) => {
                dispatch({ type: "PASSWORD_VERIFY_FULFILLED", payload: response.data })
                console.log('fullfill', response.data)
            })
            .catch((err) => {
                dispatch({ type: "PASSWORD_VERIFY_REJECTED", payload: err.response.data })
                console.log('error', err)
            })
    }
}

