export default function reducer(state = {
    topDobFilm: {},
    fetching: false,
    fetched: false,
    error: null,
}, action) {

    switch (action.type) {
        case "TOP_DOB_FILM": {
            return { ...state, fetching: true }
        }
        case "TOP_DOB_FILM_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "TOP_DOB_FILM_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                topDobFilm: action.payload,
            }
        }
    }

    return state
}
