export default function reducer(state = {
    user: {},
    responseKey: null,
    fetching: false,
    fetched: false,
    error: null,
    isLogin: false,
}, action) {

    switch (action.type) {
        case "LOGIN": {
            return { ...state, fetching: true }
        }
        case "LOGIN_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "LOGIN_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                responseKey: action.payload.key,
                isLogin: true,
                user:action.payload.user
            }
        }
        case "LOGOUT": {
            return {
                ...state,
                fetching: false,
            }
        }
        case "LOGOUT": {
            return { ...state, fetching: true }
        }
        case "LOGOUT_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                responseKey: null,
                isLogin: false,
                user:{}
            }
        }
    }

    return state
}
