import React, { Component } from 'react';
import InputBox from '../../components/inputBox/InputBox'
import validator from 'validator';
import queryString from 'query-string'
import { Link, withRouter } from 'react-router-dom'
import { connect } from "react-redux"
import { passwordVerify } from "../../actions/passwordVerifyAction";
import '../login/logIn.scss';

class PasswordVerify extends Component {
    constructor() {
        super()
        this.state = {
            formData: {
                username: '',
                token: '',
                new_password1: '',
                new_password2: '',
            },
            errors: {},
            serverError: false,
            serverErrorMsg: "",
        }
        this.upDateData = this.upDateData.bind(this);
        this.reset = this.reset.bind(this);
    }

    componentWillMount() {
        const values = queryString.parse(this.props.location.search)
        var queryData = {
            username: values.username,
            token: values.token,
            new_password1: '',
            new_password2: '',
        }
        this.setState({ formData: queryData })

    }

    upDateData(e, type) {
        let data = this.state.formData;
        data[e.target.name] = e.target.value;
        this.setState({ formData: data })
    }

    signupFormValidator(data) {
        let errors = {};


        if (validator.isEmpty(data.new_password1)) {
            errors.new_password1 = 'Password is requred';
        } else if (!validator.matches(data.new_password1)) {
            errors.new_password1 = 'Must contain at least 1 lowercase,1 uppercase, 1 number and minimum 8 charactors';
        }

        if (validator.isEmpty(data.new_password2)) {
            errors.new_password2 = 'Confirm Password is requred';
        } else if (data.new_password1 !== data.new_password2) {
            errors.new_password2 = 'password mismatch ';
        }

        return {
            errors,
            isValid: Object.keys(errors).length === 0 && errors.constructor === Object
        }
    }

    reset() {
        this.setState({
            serverError: false,
            serverErrorMsg: ""
        })
        const { errors, isValid } = this.signupFormValidator(this.state.formData);
        this.setState({ errors: errors })

        if (isValid) {
            const values = queryString.parse(this.props.location.search)
            this.props.dispatch(passwordVerify(this.state.formData));
        }
    }

    render() {
        const { errors } = this.state;
        const { error = {}, fetched, verifyPass = {} } = this.props;

        if (error) {
            var errorText = "";
            error.errors.forEach(function (arrayItem) {
                errorText += ' ' + arrayItem.message.join(" ");
            });
        }

        return (
            <div className="login-form">
                <div className="container">
                    <div className="login-content">
                        <h2>Reset Password</h2>
                        {error ? (
                            <p className="error-text">
                                {errorText}
                            </p>
                        ) : ''}
                        {fetched ? (<p className="confirmation-text">{Object.values(verifyPass)} Now you can <Link to={"/login"}>Login from here</Link></p>
                        ) : ''}
                        <form>
                            <InputBox type="password" placeholder="New-Password" name="new_password1" onUpdate={this.upDateData} error={errors.new_password1} />
                            <InputBox type="password" placeholder="Confirm Password" name="new_password2" onUpdate={this.upDateData} error={errors.new_password2} />
                            <div className="input-field login-btn " >
                                <div className="btn" onClick={this.reset} >Reset</div>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        );
    }
}
export default withRouter(connect((store) => {
    // console.log(store);
    return {
        verifyPass: store.verifyPass.verifyPass,
        fetching: store.verifyPass.fetching,
        fetched: store.verifyPass.fetched,
        error: store.verifyPass.error,
    };
})(PasswordVerify))
