export default function reducer(state = {
    user: {},
    fetching: false,
    fetched: false,
    error: null,
    confirmKey: {},
}, action) {

    switch (action.type) {
        case "SIGNUP": {
            return { ...state, fetching: true, user: action.payload }
        }
        case "SIGNUP_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "SIGNUP_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                confirmKey: action.payload,
            }
        }
    }

    return state
}