import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function passwordRest(data) {
    return function (dispatch) {
        dispatch({ type: "PASSWORD_RESET", payload: data });

        axios.post(apiUrl + '/apiv1/auth/password/reset/', data)
            .then((response) => {
                dispatch({ type: "PASSWORD_RESET_FULFILLED", payload: response.data })
                console.log('fullfill', response.data)
            })
            .catch((err) => {
                dispatch({ type: "PASSWORD_RESET_REJECTED", payload: err.response.data })
                console.log('error', err)
            })
    }
}

