import axios from "axios";
const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;
const apiUrl = process.env.REACT_APP_API_URL;

export function logout(data, ownProps) {
    return function (dispatch) {
        dispatch({ type: "LOGOUT"});
        axios.post(apiUrl + '/apiv1/auth/logout/', data, { headers: { 'Authorization': 'Token ' + localStorage.getItem('token') } })
            .then((response) => {
                dispatch({ type: "LOGOUT_FULFILLED", payload: response.data })
                localStorage.clear();
                ownProps.history.push(homeUrl + '/login');
            })
            .catch((err) => {
                dispatch({ type: "LOGOUT_FULFILLED", payload: {} })
                localStorage.clear();
                ownProps.history.push(homeUrl + '/login');
            })
    }
}
