export default function reducer(state = {
    verifyPass: {},
    fetching: false,
    fetched: false,
    error: null,
}, action) {

    switch (action.type) {
        case "PASSWORD_VERIFY": {
            return { ...state, fetching: true, verifyPass: action.payload, }
        }
        case "PASSWORD_VERIFY_REJECTED": {
            return { ...state, fetched: false, fetching: false, error: action.payload }
        }
        case "PASSWORD_VERIFY_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                verifyPass: action.payload,
            }
        }
    }

    return state
}
