import { combineReducers } from "redux"
import user from "./signupReducer"
import auth from "./loginReducer"
import verify from "./confirmEmailReducer"
import resetPass from "./passwordResetReducer"
import verifyPass from "./passwordVerifyReducer"
import userDetail from "./userDetailReducer"
import filmList from "./filmListReducer"
import searchFilmResult from "./searchFilmReducer"
import resultListFilm from "./resultListFilmReducer"
import topDobFilm from "./topDobFilmReducer"
import logout from "./logoutReducer"
import sideBar from "./sideBarReducer"
import filmData from "./filmDataReducer"
import addProject from "./addProjectReducer"

export default combineReducers({
    user,
    auth,
    verify,
    resetPass,
    verifyPass,
    userDetail,
    filmList,
    searchFilmResult,
    resultListFilm,
    topDobFilm,
    logout,
    sideBar,
    filmData,
    addProject
})