import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function searchFilmResult(data, searchtext) {
    return function (dispatch) {
        dispatch({ type: "SEARCH_FILM", data });
        axios.get(apiUrl + '/apiv1/films/?search=' + searchtext, { headers: { 'Authorization': 'Token ' + data } })
            .then((response) => {
                dispatch({ type: "SEARCH_FILM_FULFILLED", payload: response.data.results })
            })
            .catch((err) => {
                dispatch({ type: "SEARCH_FILM_REJECTED", payload: err.response })
            })
    }
}

