import React, { Component } from 'react';
import axios from "axios";
import './itemList.scss';
import dummy from '../../assets/images/dummy.png';
const apiUrl = process.env.REACT_APP_API_URL;

 
class ItemCard extends Component {
    constructor() {
        super();
        this.state = {
            imageUrl: null,
            
        };
    }

    componentDidMount() {
        var token = localStorage.getItem('token');
        fetch(apiUrl + this.props.data.poster_url, {
            headers: new Headers({
                'Authorization': `Token ` + token,
                'Content-Type': 'application/json'
            })
        }).then(response => {
            if (response.statusText === 'OK') {
                this.setState({ imageUrl: response.url })
            }
        })
    }


    render() {
        const { title, data } = this.props;

        return (
            <div className="list-item">
                <div className="item-poster">
                    <img src={this.state.imageUrl ? this.state.imageUrl : dummy} />
                    <div className="hover">
                        <span>Release date</span>
                        <span className="date">{data.domestic_release_date}</span>
                    </div>
                </div>
                <div className="item-details">
                    <span>{data.title}</span>
                    <span>
                        <span>WWOB</span>
                        <span className="income">{data.dbo}</span>
                    </span>
                </div>
            </div>
        );
    }
}
export default ItemCard
