import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function topDobFilm(data) {
    return function (dispatch) {
        dispatch({ type: "TOP_DOB_FILM", data });
        axios.get(apiUrl + '/apiv1/films/?ordering=-film_project__dbo', { headers: { 'Authorization': 'Token ' + data } })
            .then((response) => {
                dispatch({ type: "TOP_DOB_FILM_FULFILLED", payload: response.data.results })
            })
            .catch((err) => {
                dispatch({ type: "TOP_DOB_FILM_REJECTED", payload: err.response })
            })
    }
}

