import React, { Component } from 'react';
import { connect } from "react-redux"
import { Link } from "react-router-dom";
import './header.scss';
import Logo from '../../assets/images/logo.png';
import  UserMenu from "./userMenu";
import { sideBar } from "../../actions/sideBarAction";

const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;


class Header extends Component {

    componentWillMount() {
    }

    sideBar(){
        this.props.dispatch(sideBar());
    }

    render() {
        let { isLogin, sideBar } = this.props;

        return (
            <header>
                <div className="header">
                    <a  className={sideBar? "hamburger active" :"hamburger"} onClick={() => this.sideBar()}><span></span></a>  
                    <div className="logo">
                        <a href="/"><img src={Logo} /></a>
                    </div>
                    {!isLogin ? <div className="login">
                        <Link to={homeUrl + "/login"}>
                            <button>
                                Login
                             </button>
                        </Link>
                    </div> : <UserMenu/>}
                </div>
            </header>
        );
    }
}

export default connect((store) => {
    return {
        isLogin: store.auth.isLogin,
        userDetail: store.userDetail.userDetail,
        responseKey: store.auth.responseKey,
        sideBar:store.sideBar.sideBar
    };
})(Header)

