import React, { Component } from 'react';
import { connect } from "react-redux";
import './logIn.scss';
import LoginOption from './loginOption';
import { Link, withRouter } from "react-router-dom";
import InputBox from '../../components/inputBox/InputBox'
import validator from 'validator';
import { logIn } from "../../actions/loginAction";
import { confirmEmail } from "../../actions/confirmEmailAction";
import { auth, requireAuth } from './../../authentication/requireAuth';

const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;

class Login extends Component {
    constructor() {
        super()
        this.state = {
            formData: {
                email: '',
                password: '',
            },
            errors: {},
            serverError: false,
            serverErrorMsg: "",
        }
        this.upDateData = this.upDateData.bind(this);
        this.login = this.login.bind(this);
    }

    componentWillMount() {
        auth(this.props);
    }

    upDateData(e, type) {
        let data = this.state.formData;
        data[e.target.name] = e.target.value;
        this.setState({ formData: data })
    }

    loginFormValidator(data) {
        let errors = {};

        if (validator.isEmpty(data.email)) {
            errors.email = 'Email is requred';
        } else if (!validator.isEmail(data.email)) {
            errors.email = 'Email is not valid';
        }

        if (validator.isEmpty(data.password)) {
            errors.password = 'Password is requred';
        } else if (!validator.matches(data.password)) {
            errors.password = 'Must contain at least 1 lowercase,1 uppercase, 1 number and minimum 8 charactors';
        }
        return {
            errors,
            isValid: Object.keys(errors).length === 0 && errors.constructor === Object
        }
    }

    login() {
        this.setState({
            serverError: false,
            serverErrorMsg: ""
        })
        const { errors, isValid } = this.loginFormValidator(this.state.formData);
        this.setState({ errors: errors })

        if (isValid) {
            this.props.dispatch(logIn(this.state.formData, this.props));
        }

    }

    render() {
        const { errors, serverError, serverErrorMsg } = this.state;
        const { error = {}, fetching } = this.props;

        if (error) {
            var errorText = "";
            error.errors.forEach(function (arrayItem) {
                errorText += ' ' + arrayItem.message.join(" ");
            });
        }

        return (
            <div className="login-form">
                <div className="container">
                    <div className="login-content">
                        <h2>Welcome back, to Hollywoodintel</h2>
                        {error ? (
                            <p className="error-text">
                                {errorText}
                            </p>
                        ) : ''}
                        <form>
                            <InputBox type="text" placeholder="Email Address" name="email" onUpdate={this.upDateData} error={errors.email} />
                            <InputBox type="password" placeholder="Password" name="password" onUpdate={this.upDateData} error={errors.password} />
                            <div className="new-account">
                                <Link className="forgot-password" to={homeUrl + "/passwordReset"}>Forgot password?</Link>
                                <Link to={homeUrl + "/signin"}>Create a new account</Link>
                            </div>
                            <div className="input-field login-btn">
                                <div className="btn" onClick={this.login} >
                                    {fetching ? (
                                        <div className="loader"></div>
                                    ) : 'Login'}
                                </div>
                            </div>
                        </form>
                        <div className="or">
                            <div className="line"></div>
                            <p>OR</p>
                            <div className="line"></div>
                        </div>
                        <LoginOption />
                    </div>
                </div>
            </div>
        );
    }
}




export default withRouter(connect((store) => {
    return {
        user: store.auth.user,
        fetching: store.auth.fetching,
        fetched: store.auth.fetched,
        error: store.auth.error,
        isLogin: store.auth.isLogin,
        responseKey: store.auth.key,
    };
})(Login))
