import React, { Component } from 'react';
import './searchBox.scss'
import { DebounceInput } from 'react-debounce-input';
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { searchFilmResult } from "../../actions/searchFilmAction";
import { resultListFilm } from "../../actions/resultListFilmAction";
import SearchResult from './serachResult'


class SearchBox extends Component {
    constructor() {
        super()
        this.state = {
            searchText: '',
            openSearchResult: false,
            data: {},
        }
        this.upDateData = this.upDateData.bind(this);
        this.search = this.search.bind(this);
        this.openSearch = this.openSearch.bind(this);

    }

    upDateData(e) {
        let text = this.state.searchText;
        text = e.target.value;
        this.setState({ searchText: text });
        var token = localStorage.getItem('token');
        if (this.props.searchType == 'film') {
            this.props.dispatch(searchFilmResult(token, this.state.searchText));
        }
    }

    openSearch() {
        // let openSearchResult = this.state.openSearchResult;
        // openSearchResult = !openSearchResult;
        this.setState({ openSearchResult: true })
    }

    search() {
        this.setState({ openSearchResult: false })
        this.props.dispatch(resultListFilm(this.props.searchFilmResult));
    }

    render() {
        const { searchType, searchFilmResult, fetching } = this.props;
        const { openSearchResult, data } = this.state;

        return (
            <div className="search-area">
                <div className="search-box">
                    <DebounceInput minLength={3} debounceTimeout={500} type="text" onChange={(e) => this.upDateData(e)} onClick={this.openSearch} />
                    <div className="btn" onClick={this.search} >
                        {fetching ? (
                            <div className="loader"></div>
                        ) : 'Search'}
                    </div>

                </div>

                {searchType == 'film' ? (
                    openSearchResult ? (
                        <SearchResult data={searchFilmResult} />
                    ) : ''
                ) : ''}

            </div>
        );
    }
}

export default withRouter(connect((store) => {
    return {
        responseKey: store.auth.key,
        searchFilmResult: store.searchFilmResult.searchFilmResult,
        fetching: store.searchFilmResult.fetching,
        resultListFilm: store.resultListFilm.resultListFilm,
    };
})(SearchBox))