import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function userDetail(data) {
    return function (dispatch) {
        dispatch({ type: "USER_DETAIL", data });
        axios.get(apiUrl + '/apiv1/auth/user/', { headers: { 'Authorization': 'Token ' + data } })
            .then((response) => {
                dispatch({ type: "USER_DETAIL_FULFILLED", payload: response.data })
            })
            .catch((err) => {
                dispatch({ type: "USER_DETAIL_REJECTED", payload: err.response })
            })
    }
}

