import './logIn.scss';
import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { withRouter } from 'react-router-dom'
import validator from 'validator';
import { connect } from "react-redux"
import axios from 'axios';
import LoginOption from './loginOption';
import InputBox from '../../components/inputBox/InputBox'
import { signUp } from "../../actions/signupAction"
import SignUpImg from '../../assets/images/signup-img.png';
const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;


class SignIn extends Component {
    constructor() {
        super()
        this.state = {
            formData: {
                username: '',
                password1: '',
                password2: '',
                email: '',
            },
            errors: {},
            serverError: false,
            serverErrorMsg: "",
        }
        this.upDateData = this.upDateData.bind(this);
        this.signup = this.signup.bind(this);
    }
    upDateData(e, type) {
        let data = this.state.formData;
        data[e.target.name] = e.target.value;
        this.setState({ formData: data })
    }

    signupFormValidator(data) {
        let errors = {};

        if (validator.isEmpty(data.username)) {
            errors.username = 'First name is requred';
        }

        if (validator.isEmpty(data.email)) {
            errors.email = 'Email is requred';
        } else if (!validator.isEmail(data.email)) {
            errors.email = 'Email is not valid';
        }

        if (validator.isEmpty(data.password1)) {
            errors.password1 = 'Password is requred';
        } else if (!validator.matches(data.password1)) {
            errors.password1 = 'Must contain at least 1 lowercase,1 uppercase, 1 number and minimum 8 charactors';
        }

        if (validator.isEmpty(data.password2)) {
            errors.password2 = 'Confirm Password is requred';
        } else if (data.password1 != data.password2) {
            errors.password2 = 'password mismatch ';
        }

        return {
            errors,
            isValid: Object.keys(errors).length === 0 && errors.constructor === Object
        }
    }

    signup() {
        this.setState({
            serverError: false,
            serverErrorMsg: ""
        })
        const { errors, isValid } = this.signupFormValidator(this.state.formData);
        this.setState({ errors: errors })

        if (isValid) {
            this.props.dispatch(signUp(this.state.formData));
        }
    }

    render() {
        const { errors, serverError, serverErrorMsg } = this.state;
        const { error, fetched, fetching } = this.props;

        if (error) {
            var errorText = "";
            error.errors.forEach(function (arrayItem) {
                errorText += ' ' + arrayItem.message.join(" ");
            });
        }

        return (
            <div className="login-form">
                <div className="left-side">
                    <div className="login-content">
                        <h2>Sign Up</h2>
                        {error ? (
                            <p className="error-text">
                                {errorText}
                            </p>
                        ) : ''}
                        {fetched ? (
                            <p className="confirmation-text">
                                Please check your email and confirm it.
                            </p>
                        ) : ''}
                        <form>
                            <InputBox type="text" placeholder="First Name" name="username" onUpdate={this.upDateData} error={errors.username} />
                            <InputBox type="text" placeholder="Email Address" name="email" onUpdate={this.upDateData} error={errors.email} />
                            <InputBox type="password" placeholder="Password" name="password1" onUpdate={this.upDateData} error={errors.password1} />
                            <InputBox type="password" placeholder="Confirm Password" name="password2" onUpdate={this.upDateData} error={errors.password2} />

                            <div className="new-account">
                                <div></div>
                                <Link className="loginLink" to={homeUrl + "/login"}>Already have an account</Link>
                            </div>
                            <div className="input-field login-btn" >
                                <div className="btn" onClick={this.signup} >
                                    {fetching ? (
                                        <div class="loader"></div>
                                    ) : 'Sign Up'}</div>
                            </div>
                        </form>
                        <div className="or">
                            <div className="line"></div>
                            <p>OR</p>
                            <div className="line"></div>
                        </div>
                        <LoginOption />
                    </div>
                </div>
                <div className="right-side">
                    <div className="right-side-content">
                        <h2>
                            Welcome to Hollywoodintel
                        </h2>
                        <p>"Lorem ipsum dolor sit amet, consectetur adipiscing elit"</p>
                        <div className="signup-img">
                            <img src={SignUpImg} />
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default withRouter(connect((store) => {
    console.log(store);
    return {
        user: store.user.user,
        fetching: store.user.fetching,
        fetched: store.user.fetched,
        error: store.user.error,
        confirmKey: store.user.confirmKey,
    };
})(SignIn)) 