import React, { Component } from 'react';
import { connect } from "react-redux"
import './menuList.scss';
import MenuName from './menuName';
import { Link } from "react-router-dom";
import Arrow from '../../assets/images/arrow.svg'

const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;


class MenuList extends Component {
    constructor() {
        super()
        this.state = {
            menutitle: '',
            iconName: '',
            menu: {
                film: false,
                cast: false,
                tool: false
            }
        }

        this.menuOpen = this.menuOpen.bind(this);
    }
    menuOpen(menuItem) {
        let menu = this.state.menu;
        menu[menuItem] = !menu[menuItem];
        this.setState({ menu: menu })
    }


    render() {
        const { menu } = this.state;
        const { sideBar } = this.props;

        return (
            <div className={sideBar? "side-menu active" :"side-menu"}>
                <ul className="menu-list">
                    <li onClick={() => this.menuOpen('film')} className={menu.film ? 'active' : ''}>
                        <MenuName menutitle="Film" iconName="Film" />
                        <div className="arrow">
                            <img src={Arrow} />
                        </div>
                        <ul>
                            <li>
                                <Link to={homeUrl + "/filmSearch"}>
                                    <MenuName menutitle="Film Serach" iconName="search" />
                                </Link>
                            </li>
                            <li>
                                <MenuName menutitle="Film Release" iconName="calender" />
                            </li>
                        </ul>
                    </li>
                    <li onClick={() => this.menuOpen('cast')} className={menu.cast ? 'active' : ''}>
                        <MenuName menutitle="Cast and Crew" iconName="Cast and Crew" />
                        <div className="arrow">
                            <img src={Arrow} />
                        </div>
                        <ul>
                            <li>
                                <MenuName menutitle="Cast Serach" iconName="search" />
                            </li>
                            <li>
                                <MenuName menutitle="Cast Release" iconName="calender" />
                            </li>
                        </ul>

                    </li>
                    <li onClick={() => this.menuOpen('tool')} className={menu.tool ? 'active' : ''}>
                        <MenuName menutitle="Tools" iconName="Tools" />
                        <div className="arrow">
                            <img src={Arrow} />
                        </div>

                        <ul>
                            <li>
                                <Link to={homeUrl + "/compBuilder"}>
                                    <MenuName menutitle="Comp Builder" iconName="comp" />
                                </Link>
                            </li>
                            <li>
                                <MenuName menutitle="Comp List" iconName="list" />
                            </li>
                        </ul>

                    </li>
                </ul>
            </div>
        );
    }
}


export default connect((store) => {
    return {
        sideBar:store.sideBar.sideBar
    };
})(MenuList)

