import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import HomePage from './pages/homePage/homePage';
import Login from './pages/login/logIn';
import SignIn from './pages/login/signIn';
import ConfirmEmail from './pages/confirmEmail/confirmEmail';
import Dashboard from './pages/dashboard/dashboard';
import PasswordReset from './pages/userPassword/passwordReset';
import PasswordVerify from './pages/userPassword/passwordVerify';
import FilmSearch from './pages/film/filmSearch'
import CompBuilder from './pages/tools/compBuilder'

const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;
// const apiUrl = process.env.REACT_APP_API_URL;

class Routes extends Component {
    render() {
        return (
            <div>
                <Route exact path={homeUrl + "/"} component={HomePage} />
                <Route exact path={homeUrl + "/login"} component={Login} />
                <Route exact path={homeUrl + "/signin"} component={SignIn} />
                <Route exact path={homeUrl + "/filmSearch"} component={FilmSearch} />
                <Route exact path={homeUrl + "/compBuilder"} component={CompBuilder} />
                <Route exact path={homeUrl + "/verify-email"} component={ConfirmEmail} />
                <Route exact path={homeUrl + "/passwordReset"} component={PasswordReset} />
                <Route exact path={homeUrl + "/password-reset-confirm"} component={PasswordVerify} />
            </div>
        );
    }
}


export default Routes;