export default function reducer(state = {
    addProject: {},
    fetching: false,
    fetched: false,
    error: null,
}, action) {

    switch (action.type) {
        case "ADD_PROJECT": {
            return { ...state, fetching: true }
        }
        case "ADD_PROJECT_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "ADD_PROJECT_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                addProject: action.payload,
            }
        }
    }

    return state
}
