import { connect } from "react-redux";
import React, { Component } from 'react';
import axios from "axios";
import './searchBox';
import { Link, withRouter } from "react-router-dom";
import M1 from '../../assets/images/m1.png';
import { filmData } from "../../actions/filmDataAction";
import AddBtn from '../../assets/images/addBtn.png'
import { addProject } from '../../actions/addProjectAction'

const apiUrl = process.env.REACT_APP_API_URL;


class SearchCard extends Component {
    constructor() {
        super();
        this.state = {
            imageUrl: null,
            // tablerows: [
            //     {
            //         movieName: "",
            //         dob: "",
            //         iob: '',
            //         latinAmerica: '',
            //         esternEurope: '',
            //         otherEurope: '',
            //         other: '',
            //         WWBO: ''
            //     }
            // ]
            "projects": [
                {
                    "id": '',
                }
            ]
        };
        this.getFilmData = this.getFilmData.bind(this);
        this.addFilmData = this.addFilmData.bind(this);

    }

    componentWillMount() {
        var token = localStorage.getItem('token');
        fetch(apiUrl + this.props.data.poster_url, {
            headers: new Headers({
                'Authorization': `Token ` + token,
                'Content-Type': 'application/json'
            })
        })
            .then(response => {
                if (response.statusText === 'OK') {
                    this.setState({ imageUrl: response.url })
                }
            })
    }

    getFilmData() {
        this.props.dispatch(filmData(this.props.data));
    }

    addFilmData(e) {
        console.log(this.props.filmData)
        // let data = this.props.filmData.push(this.props.data);
        this.props.dispatch(filmData(this.props.data));
        // let data = this.props.filmData
        // data[e.target.name] = e.target.value;
        this.setState({ projects: this.props.filmData })
        console.log(this.state.projects)


    }

    render() {
        const { title, data, filmData } = this.props;
        return (
            <div className="search-result-list" onClick={this.getFilmData} >
                <div className="image">
                    <img src={this.state.imageUrl ? this.state.imageUrl : M1} />
                </div>
                <div className="right">
                    <div className="name">
                        {data.title}
                    </div>
                    <div className="income">
                        <span>Box-office Collection</span>
                        <span className="value">{data.dbo}</span>
                    </div>
                    <div className="add" onClick={this.addFilmData}>
                        <img src={AddBtn} />
                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(connect((store) => {
    return {
        filmData: store.filmData.filmData,
    };
})(SearchCard))

