export default function reducer(state = {
    fetching: false,
    fetched: false,
    error: null,
    isLogin: false,
}, action) {
    switch (action.type) {
        case "LOGOUT": {
            return { ...state, fetching: true }
        }
        case "LOGOUT_REJECTED": {
            return { ...state, fetching: false, isLogin:true, error: action.payload }
        }
        case "LOGOUT_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                isLogin: false,
            }
        }
    }
    return state
}
