import axios from "axios";
const homeUrl = process.env.REACT_APP_HOMEPAGE_URL;
const apiUrl = process.env.REACT_APP_API_URL;

export function logIn(data, ownProps) {
    return function (dispatch) {
        dispatch({ type: "LOGIN"});
        axios.post(apiUrl + '/apiv1/auth/login/', data)
            .then((response) => {
                dispatch({ type: "LOGIN_FULFILLED", payload: response.data })
                localStorage.setItem('token', response.data.key);
                localStorage.setItem('user', JSON.stringify(response.data.user));
                ownProps.history.push(homeUrl + '/filmSearch');
            })
            .catch((err) => {
                console.log(err);
                dispatch({ type: "LOGIN_REJECTED", payload: err.response.data })
            })
    }
}

