export default function reducer(state = {
    resetPass: {},
    fetching: false,
    fetched: false,
    error: null,
}, action) {

    switch (action.type) {
        case "PASSWORD_RESET": {
            return { ...state, fetching: true, resetPass: action.payload }
        }
        case "PASSWORD_RESET_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "PASSWORD_RESET_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                resetPass: action.payload,
            }
        }
    }

    return state
}