import React, { Component } from 'react';
import VideoBg from '../../assets/images/video-bg.png';
import ThampsUp from '../../assets/images/thampsup.png';
import GraphicsDots from '../../assets/images/GraphicsDots.png';
import './confirmEmail.scss';
import { connect } from "react-redux";
import { confirmEmail } from "../../actions/confirmEmailAction";
import { Link, withRouter } from 'react-router-dom'
import queryString from 'query-string'


class ConfirmEmail extends Component {

    constructor() {
        super()
    }


    componentWillMount() {
        const values = queryString.parse(this.props.location.search)
        var data = {
            key: values.token,
        }
        this.props.dispatch(confirmEmail(data));
    }

    render() {
        const { fetching, fetched } = this.props;
        return (
            <div className="thanks-page">
                {fetching ? (
                    <div className="loader"></div>
                ) : fetched ? <div className="thanks-page-content">
                    <div className="box">
                        <div className="images">
                            <div className="image">
                                <img src={GraphicsDots} />
                            </div>
                            <div className="image icon">
                                <img src={ThampsUp} />
                            </div>
                        </div>
                        <div className="text">
                            <p>Thank You for join us! Your account has been confirmed</p>
                        </div>
                        <div className="input-field login-btn" >
                            <Link to={"/login"} className="btn">
                                Go to Login
                            </Link>
                        </div>
                    </div>
                </div> : <div className="linkError"><p>Your link is expire or may be broken</p></div>}
                <div className="footer">
                    <div className="image video-bg">
                        <img src={VideoBg} />
                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(connect((store) => {
    return {
        key: store.verify.key,
        fetching: store.verify.fetching,
        fetched: store.verify.fetched,
        confirmKey: store.user.confirmKey,
    };
})(ConfirmEmail))
