import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function confirmEmail(data) {
    return function (dispatch) {
        dispatch({ type: "VERIFY_EMAIL" });
        axios.post(apiUrl + '/apiv1/auth/registration/verify-email/', data)
            .then((response) => {
                dispatch({ type: "VERIFY_EMAIL_FULFILLED", payload: response.data })
            })
            .catch((err) => {
                dispatch({ type: "VERIFY_EMAIL_REJECTED", payload: err.response })
            })
    }
}





