import React, { Component } from 'react';
import './filmSearch.scss'
import { requireAuth } from './../../authentication/requireAuth';
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import SearchBox from '../../components/searchBox/searchBox'
import ItemList from '../../components/itemList/itemList'
import { filmList } from "../../actions/filmListAction";
import { topDobFilm } from "../../actions/topDobFilmAction"
import MenuList from '../../components/menuList/menuList'


class FilmSearch extends Component {
    constructor(props) {
        super(props)
        this.state = {
            listtitle: '',
            data: {},
            searchType: '',
        }
    }

    componentWillMount() {
        requireAuth(this.props);
    }

    componentDidMount() {
        var token = localStorage.getItem('token');
        this.props.dispatch(filmList(token));
        this.props.dispatch(topDobFilm(token));
    }

    render() {
        const { addFilmBtn, data, searchType } = this.state;
        const { filmList, resultListFilm, topDobFilm } = this.props;

        return (
            <div className="hollywood-dashboard">
                <MenuList />
                <div className="search-content">
                    <SearchBox searchType="film" />
                    {resultListFilm.length > 0 ? <ItemList listtitle="Serach Result" data={resultListFilm} /> : ''}
                    <ItemList listtitle="Recent Movie" data={filmList} />
                    <ItemList listtitle="Top box office collection" data={topDobFilm} />
                </div>
            </div>
        );
    }
}

export default withRouter(connect((store) => {
    return {
        filmList: store.filmList.filmList,
        error: store.user.error,
        searchFilmResult: store.searchFilmResult.searchFilmResult,
        resultListFilm: store.resultListFilm.resultListFilm,
        topDobFilm: store.topDobFilm.topDobFilm,
    };
})(FilmSearch))
