export default function reducer(state = {
    filmList: {},
    fetching: false,
    fetched: false,
    error: null,
}, action) {

    switch (action.type) {
        case "FILM_LIST": {
            return { ...state, fetching: true }
        }
        case "FILM_LIST_REJECTED": {
            return { ...state, fetching: false, error: action.payload }
        }
        case "FILM_LIST_FULFILLED": {
            return {
                ...state,
                fetching: false,
                fetched: true,
                filmList: action.payload,
            }
        }
    }

    return state
}
