import React, { Component } from 'react';
import InputBox from '../../components/inputBox/InputBox'
import validator from 'validator';
import { withRouter } from 'react-router-dom'
import { connect } from "react-redux"
import { passwordRest } from "../../actions/passwordResetAction";
import '../login/logIn.scss';

class PasswordReset extends Component {
    constructor() {
        super()
        this.state = {
            formData: {
                email: '',
            },
            errors: {},
            serverError: false,
            serverErrorMsg: "",
        }
        this.upDateData = this.upDateData.bind(this);
        this.sendEmail = this.sendEmail.bind(this);
    }

    upDateData(e, type) {
        let data = this.state.formData;
        data[e.target.name] = e.target.value;
        this.setState({ formData: data })
    }

    signupFormValidator(data) {
        let errors = {};

        if (validator.isEmpty(data.email)) {
            errors.email = 'Email is requred';
        } else if (!validator.isEmail(data.email)) {
            errors.email = 'Email is not valid';
        }

        return {
            errors,
            isValid: Object.keys(errors).length === 0 && errors.constructor === Object
        }
    }

    sendEmail() {
        this.setState({
            serverError: false,
            serverErrorMsg: ""
        })
        const { errors, isValid } = this.signupFormValidator(this.state.formData);
        this.setState({ errors: errors })

        if (isValid) {
            this.props.dispatch(passwordRest(this.state.formData));
        }
    }

    render() {
        const { errors } = this.state;
        const { error = {}, fetched, resetPass = {} } = this.props;

        return (
            <div className="login-form">
                <div className="container">
                    <div className="login-content">
                        <h2>please enter your email address</h2>
                        {fetched ? (<p className="confirmation-text">{Object.values(resetPass)}</p>
                        ) : ''}
                        <form>
                            <InputBox type="text" placeholder="Email Address" name="email" onUpdate={this.upDateData} error={errors.email} />
                            <div className="input-field login-btn " >
                                <div className="btn" onClick={this.sendEmail} >Send</div>
                                <div className="btn resetPassword" >Cancle</div>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(connect((store) => {
    console.log('store', store);
    return {
        resetPass: store.resetPass.resetPass,
        fetching: store.resetPass.fetching,
        fetched: store.resetPass.fetched,
        error: store.resetPass.error,
    };
})(PasswordReset)) 