import axios from "axios";
const apiUrl = process.env.REACT_APP_API_URL;

export function filmList(data) {
    return function (dispatch) {
        dispatch({ type: "FILM_LIST", data });
        axios.get(apiUrl + '/apiv1/films?limit=5', { headers: { 'Authorization': 'Token ' + data } })
            .then((response) => {
                dispatch({ type: "FILM_LIST_FULFILLED", payload: response.data.results })
            })
            .catch((err) => {
                dispatch({ type: "FILM_LIST_REJECTED", payload: err.response.data })
            })
    }
}

